import React, { useState } from "react";
import { jsPDF } from "jspdf";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
function Canvas() {
  var [x, setColor] = useState("black");
  var flag = false,
    prevX = 0,
    currX = 0,
    prevY = 0,
    currY = 0,
    dot_flag = false,
    y = 1;

  React.useEffect(() => {
    const canvas = document.getElementById("myCanvas");
    canvas.addEventListener(
      "mousemove",
      function (e) {
        findxy("move", e);
      },
      false
    );
    canvas.addEventListener(
      "mousedown",
      function (e) {
        findxy("down", e);
      },
      false
    );
    canvas.addEventListener(
      "mouseup",
      function (e) {
        findxy("up", e);
      },
      false
    );
    canvas.addEventListener(
      "mouseout",
      function (e) {
        findxy("out", e);
      },
      false
    );
  }, [x]);

  const findxy = (res, e) => {
    const canvas = document.getElementById("myCanvas");
    const ctx = canvas.getContext("2d");

    if (res === "down") {
      prevX = currX;
      prevY = currY;
      currX = e.clientX - canvas.offsetLeft;
      currY = e.clientY - canvas.offsetTop;

      flag = true;
      dot_flag = true;
      if (dot_flag) {
        ctx.beginPath();
        ctx.fillStyle = x;
        ctx.fillRect(currX, currY, 2, 2);
        ctx.closePath();
        dot_flag = false;
      }
    }
    if (res === "up" || res === "out") {
      flag = false;
    }
    if (res === "move") {
      if (flag) {
        prevX = currX;
        prevY = currY;
        currX = e.clientX - canvas.offsetLeft;
        currY = e.clientY - canvas.offsetTop;

        draw(x);
      }
    }
  };

  const draw = (x) => {
    const ctx = document.getElementById("myCanvas").getContext("2d");

    ctx.beginPath();
    ctx.moveTo(prevX, prevY);
    ctx.lineTo(currX, currY);

    ctx.strokeStyle = x;
    ctx.lineWidth = y;
    ctx.stroke();
    ctx.closePath();
  };

  const pdf = () => {
    const canvas = document.getElementById("myCanvas");
    var imgData = canvas.toDataURL("image/png", 1);
    var pdf = new jsPDF();
    pdf.addImage(imgData, "png", 0, 0);
    pdf.save("download.pdf");
  };

  const clear = () => {
    const canvas = document.getElementById("myCanvas");
    const context = canvas.getContext("2d");

    context.clearRect(0, 0, canvas.width, canvas.height);
  };

  return (
    <Container className="border">
      <h5 className="text-center">Canvas</h5>
      <Row>
        <Col>
          <canvas
            id="myCanvas"
            width="300px"
            height="300px"
            className="border  border-2"
          ></canvas>
        </Col>
        <Col>
          <h5>Choose Color</h5>
          <Button
            variant="success"
            onClick={() => setColor("DarkGreen")}
          ></Button>{" "}
          <Button variant="danger" onClick={() => setColor("Crimson")}></Button>{" "}
          <Button
            variant="primary"
            onClick={() => setColor("DodgerBlue")}
          ></Button>{" "}
          <Button
            variant="warning"
            onClick={() => setColor("DarkOrange")}
          ></Button>{" "}
          <br />
          <br />
          <Button variant="success" onClick={() => pdf()}>
            download
          </Button>{" "}
          <Button variant="warning" onClick={() => clear()}>
            Clear
          </Button>
        </Col>
      </Row>{" "}
    </Container>
  );
}

export default Canvas;
