import axios from "axios";
import React, { useState, useEffect } from "react";
import Container from "react-bootstrap/Container";
import Card from "react-bootstrap/Card";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

const Url =
  "https://api.openweathermap.org/data/2.5/onecall?lat=51.5074&lon=0.1278&exclude=hourly,daily&appid=099f5c92834a01e7a6d2b2a5b817fcc7";

function Weather() {
  const [userData, setUserData] = useState({});

  const Weatherdetails = async () => {
    const response = await axios.get(Url);
    setUserData(response.data);
  };

  useEffect(() => {
    Weatherdetails();
  }, []);

  return (
    <Container>
      <Card>
        <Card.Body>
          <Card.Title className="text-center">Weather Details</Card.Title>
          <br />
          <Row>
            <Col>
              <Card.Text>
                Latitude :{userData.lat ? userData.lat : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Longitude :{userData.lon ? userData.lon : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Timezone :
                {userData.timezone ? userData.timezone : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Pressure :
                {userData.current
                  ? userData.current.pressure
                  : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Temperature :
                {userData.current ? userData.current.temp : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Sun Rise :
                {userData.current
                  ? userData.current.sunrise
                  : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Sun Set :
                {userData.current ? userData.current.sunset : "Not yet Updated"}
              </Card.Text>
            </Col>
            <Col>
              <Card.Text>
                Visibility :
                {userData.current
                  ? userData.current.visibility
                  : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Clouds :
                {userData.current ? userData.current.clouds : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Minimum Temperature :
                {userData.daily ? userData.daily.temp.min : "Not yet Updated"}
              </Card.Text>{" "}
              <Card.Text>
                Maximum Temperature :
                {userData.daily ? userData.daily.temp.max : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Humidity :
                {userData.current
                  ? userData.current.humidity
                  : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Wind Speed :
                {userData.current
                  ? userData.current.wind_speed
                  : "Not yet Updated"}
              </Card.Text>
              <Card.Text>
                Weather :
                {userData.hour ? userData.hour.weather.main : "Not yet Updated"}
              </Card.Text>
            </Col>
          </Row>
        </Card.Body>
      </Card>
    </Container>
  );
}

export default Weather;
